  ul li {
    color: #3F5D45;

    a img {
      margin-left: 20px;
      color: #3F5D45;
    }
  }
}

.logo {
  width: 211.22px;
  height: 66.6px;
}

ul {
  display: flex;
  justify-content: space-around;
  width: 284px;
}

